#header{
	position: relative;
	z-index: 2;
}
#content{
	width:100%;
	height: 0;
	position: absolute;
	top: 0px;
	opacity: 0;
	z-index: 1;
	-webkit-transition: all .2s;
	transition: all .2s;
	padding: 50px auto;
}
#content .ct-box{
	display: none;
	opacity: 0;
	height: 260px;
	margin-top: 0px;
}
.nav-global .nav-list .on{
	color: #00c3f5;
	-webkit-transition: all .2s;
	transition: all .2s;
}
.product{
	margin: 50px 280px 50px 350px;
}
