	width: 100px;
	height: 100%;
	padding: 50px 10px;
	color: red;
	float: left;
	cursor: pointer;
}
.pt_desc{
	color: #666;
	text-align: center;
	font-size: 14px;
	font-weight: bold;
}
.pt_price{
	text-align: center;
	color: #ff6700;
}
.ad_box{
	margin: 50px 150px;
	padding: 80px 180px 20px 180px;
}




































































































































































































@charset "utf-8";
/* CSS Document */
/*html,body { user-select: none; -moz-user-select: none; -webkit-user-select: none; }*/
body,
ul,
ol,
dl,
dd,
h1,
h2,
h3,
h4,
h5,
h6,
p,
input,
select,
textarea,
form {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: 100%;
    font-weight: 400;
}
html {
  height: 100%;
  background: #ededed;
}
body {
  font-family: "PingFang SC", "Helvetica Neue", Helvetica, Arial, "Hiragino Sans GB", "Microsoft Yahei", 微软雅黑, STHeiti, 华文细黑, sans-serif;
  color: rgb(102, 102, 102);
  font-size: 14px;
  min-width: 1220px;
}
li {
  list-style: none;
}
img {
  border: none;
  display: block;
}
input,
select,
textarea,
button {
  font-family: tahoma, arial, 'Hiragino Sans GB', '\5FAE\8F6F\96C5\9ED1', sans-serif;
  outline: none;
  border: none;
  background-color: transparent;
}
em,
i,
dfn {
  font-style: normal;
}
textarea {
  resize: none;
  overflow: auto;
  outline: none;
}
a {
  text-decoration: none;
  color: #666;
  outline: none;
  cursor: pointer;
}
a:hover {
  outline: none;
}
button {
  cursor: pointer;
}
button:focus {
  border: none;
  outline: none;
}
pre {
  white-space: pre-wrap!important;
  word-wrap: break-word!important;
  *white-space:normal!important;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.clear:after,
.clear:before {
  content: "";
  display: table;
}
.clear:after {
  clear: both;
}
.clear {
  *zoom: 1;
}
.store-content{
	width: 1220px;
	height: 100%;
	padding: 0 0 25px;
	margin: 0 auto;
	position: relative;
}
.hide{
	display: none!important;
}
.fn-left, .fn-right{
	display: inline;
}
.fn-left{
	float: left;
}
.fn-right{
	float: right;
}@charset "utf-8";

.nav-global {
  height: 80px;
  position: relative;
  background: rgba(255, 255, 255);
}

.container {
  height: 100%;
  width: 1240px;
  margin: 0 auto;
}

/*logo*/

.nav-logo {
  width: 125px;
  height: 80px;
  line-height: 80px;
  float: left;
}

.nav-logo a {
  display: block;
  width: 125px;
  height: 80px;
  line-height: 80px;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 125px 23px;
}

/*导航*/

.nav-global .nav-list {
  float: right;
  color: #ccc;
  height: 80px;
  line-height: 80px;
  position: relative;
}

.nav-global .nav-list li {
  float: left;
}

.nav-global .nav-list a{
  display: inline-block;
  font-size: 12px;
  padding: 0 14px;
  color: #333;
  transition: color .2s linear;
  -webkit-transition: color .2s linear;
  -moz-transition: color .2s linear;
  -ms-transition: color .2s linear;
  -o-transition: color .2s linear;
}

.nav-global .nav-list .search {
  width: 176px;
  height: 30px;
  color: #eee;
  padding-left: 23px;
  border: 1px solid #eee;
  border-radius: 50px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  -o-border-radius: 50px;
}

.nav-global .nav-list a img{
  width: 20px;
  height: 20px;
  top: 28px;
  right: 18px;
  opacity: .5;
  position: absolute;
}

.nav-global .nav-list li:active {
  color: rgba(24, 186, 253, .9);
}

/* .nav-global .nav-list a:hover {
  color: rgba(24, 186, 253, .9);
} */

.nav-aside {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  float: right;
  margin-top: 38px;
  border-left: 1px solid rgba(255, 255, 255, .2);
}

.nav-aside>li {
  position: relative;
  float: left;
}

.nav-aside>li>a {
  font-size: 0;
}

/*用户列表*/

.nav-user {
  margin-left: 40px;
  width: 36px;
}

.nav-user>a:before {
  content: " ";
  position: absolute;
  left: 8px;
  width: 20px;
  top: -8px;
  height: 20px;
  background: url(data:image/png;base64,) -155px 0;
  background-size: 240px 107px;
  -webkit-transition: none;
  transition: none;
}

.nav-user:hover>a:before {
  background-position: -5px 0;
}

.nav-user-wrapper {
  position: absolute;
  z-index: 30;
  padding-top: 18px;
  opacity: 0;
  visibility: hidden;
  top: -3000px;
  width: 168px;
  left: 50%;
  margin-left: -70px;
}

.nav-user:hover .nav-user-wrapper {
  top: 18px;
  visibility: visible;
  opacity: 1;
  -webkit-transition: opacity .15s ease-out;
  transition: opacity .15s ease-out;
}

.nav-user-list {
  position: relative;
  width: 128px;
  padding-top: 20px;
  background: #fff;
  border: 1px solid #d6d6d6;
  border-color: rgba(0, 0, 0, .08);
  border-radius: 8px;
  -webkit-box-shadow: 0 20px 40px rgba(0, 0, 0, .15);
          box-shadow: 0 20px 40px rgba(0, 0, 0, .15)
}

.nav-user-list:before {
  position: absolute;
  content: " ";
  background: url(data:image/png;base64,) no-repeat -49px -43px;
  background-size: 240px 107px;
  width: 20px;
  height: 8px;
  left: 64%;
  top: -8px;
  margin-left: -25px;
}

.nav-user-avatar>dd {
  display: block;
  position: relative;
  margin: 0 auto;
  width: 46px;
  height: 46px;
  margin-bottom: 8px;
}

.nav-user-avatar>dd:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 50%;
  -webkit-box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .06);
          box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .06);
}

.nav-user-avatar span {
  border-radius: 50%;
  display: block;
  width: 100%;
  height: 100%;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}

.nav-user-avatar dt {
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 1.5;
  text-align: center;
  color: #757575;
}

.nav-user-list li a {
  position: relative;
  border-top: 1px solid #f5f5f5;
  display: block;
  padding-left: 50px;
  line-height: 28px;
  height: 28px;
  color: #616161;
  font-size: 12px;
}

.nav-user-list li a:hover {
  background-color: #fafafa;
}

.nav-user-list li a:before {
  background: url(data:image/png;base64,) no-repeat;
  background-size: 240px 107px;
  content: ' ';
  display: block;
  position: absolute;
  left: 24px;
  top: 50%;
  width: 20px;
  height: 20px;
  margin-top: -8px;
}

.nav-user-list li.order a:before {
  background-position: 0 -44px;
}

.nav-user-list li.support a:before {
  background-position: -20px -44px;
}

.nav-user-list li.coupon a:before {
  background-position: -20px -84px;
}

.nav-user-list li.information a:before {
  background-position: 0 -64px;
}

.nav-user-list li.address a:before {
  background-position: -20px -64px;
}

.nav-user-list li.logout a:before {
  background-position: 0 -84px;
}

/*购物车*/

.nav-cart {
  margin-left: 21px;
  width: 61px;
}

.nav-cart>a {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1;
}

.nav-cart>a:before {
  content: ' ';
  position: absolute;
  left: 0;
  top: -8px;
  bottom: 0;
  width: 30px;
  height: 20px;
  background: url(data:image/png;base64,) -150px -22px;
  background-size: 240px 107px;
}

.nav-cart:hover>a:before {
  background-position: 0 -22px;
}

.cart-empty-num {
  position: relative;
  margin-left: 20px;
  top: -18px;
  min-width: 30px;
  text-indent: 0;
  z-index: 1;
}

.cart-empty-num i {
  font-style: normal;
  display: inline-block;
  width: 20px;
  height: 20px;
  line-height: 20px;
  border-radius: 10px;
  color: #fff;
  font-size: 12px;
  text-align: center;
  background: #ff6700;
}

.cart-num i {
  background: #ff6700;
  -webkit-box-shadow: inset 0 0 1px rgba(255, 255, 255, .15), 0 1px 2px rgba(255, 255, 255, .15);
          box-shadow: inset 0 0 1px rgba(255, 255, 255, .15), 0 1px 2px rgba(255, 255, 255, .15);
}

.nav-cart-wrapper {
  position: absolute;
  right: 0;
  padding-top: 18px;
  top: 18px;
  z-index: 30;
  width: 360px;
}

.nav-cart-wrapper:before {
  position: absolute;
  content: ' ';
  background: url(data:image/png;base64,) no-repeat -49px -43px;
  background-size: 240px 107px;
  width: 20px;
  height: 8px;
  right: 34px;
  top: 10px;
  z-index: 11;
}

.nav-cart.active .nav-cart-wrapper {
  display: block;
  top: 18px;
  opacity: 1;
  visibility: visible;
}

.nav-cart-list {
  position: relative;
  z-index: 10;
  background: #fff;
  border: 0 solid rgba(255, 255, 255, .01);
  -webkit-box-shadow: 0 0 0 1px rgba(0, 0, 0, .06), 0 20px 40px rgba(0, 0, 0, .15);
          box-shadow: 0 0 0 1px rgba(0, 0, 0, .06), 0 20px 40px rgba(0, 0, 0, .15);
  overflow: hidden;
}

.nav-cart-list .empty {
  overflow: hidden;
  height: 134px;
  padding-top: 180px;
  background: url(data:image/png;base64,) center 90px no-repeat;
  background-size: auto 62px;
  text-align: center;
}

.nav-cart-list .empty h3 {
  font-size: 18px;
  color: #333;
  margin-bottom: 7px;
}

.nav-cart-list .empty p {
  font-size: 12px;
  color: #bcbcbc;
}

.nav-cart-list .full {
  border-radius: 8px;
  overflow: hidden;
}

.nav-cart-list .nav-cart-items {
  max-height: 665px;
  overflow-x: hidden;
  overflow-y: auto;
}

.nav-cart-list .cart-item {
  height: 120px;
  width: 100%;
  overflow: hidden;
  border-top: 1px solid #f0f0f0;
}

.nav-cart-list .cart-item:first-child {
  overflow: hidden;
}

.nav-cart-list .cart-item-inner {
  height: 80px;
  padding: 20px;
  position: relative;
}

.nav-cart-list .item-thumb {
  position: relative;
  float: left;
  width: 80px;
  height: 80px;
  border-radius: 3px;
}

.nav-cart-list .item-thumb:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 2;
  border: 1px solid #f0f0f0;
  border: 0 solid transparent;
  -webkit-box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .06);
          box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .06);
  border-radius: 3px;
}

.nav-cart-list .item-thumb img {
  display: block;
  width: 80px;
  height: 80px;
  border-radius: 3px;
  overflow: hidden;
}

.nav-cart-list .item-desc {
  margin-left: 98px;
  display: table;
  width: 205px;
  height: 80px;
}

.nav-cart-list .cart-cell {
  display: table-cell;
  vertical-align: middle;
}

.nav-cart-list .item-desc h4,
.nav-cart-list .item-desc h4 a {
  color: #000;
}

.nav-cart-list .item-desc h4 {
  width: 185px;
  overflow: hidden;
  word-break: keep-all;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 10px;
}

.nav-cart-list .item-desc .attrs {
  font-size: 0;
}

.nav-cart-list .item-desc .attrs span {
  position: relative;
  display: inline-block;
  margin-right: 20px;
  font-size: 14px;
  line-height: 14px;
  color: #999;
}

.nav-cart-list .item-desc .attrs span:last-child {
  margin-right: 0;
}

.nav-cart-list .item-desc h6 {
  color: #424242;
  font-size: 12px;
  line-height: 14px;
  margin-top: 20px;
}

.nav-cart-list .del-btn {
  cursor: pointer;
  display: none;
  overflow: hidden;
  width: 20px;
  height: 20px;
  position: absolute;
  right: 20px;
  top: 50%;
  margin-top: -11px;
  background: url(data:image/png;base64,) -50px -60px no-repeat;
  background-size: 240px 107px;
  text-indent: -9999em;
}

.nav-cart-list .cart-item:hover .del-btn {
  display: block;
}

.nav-cart-list .del-btn:hover {
  background-position: -75px -60px;
}

.nav-cart-list .nav-cart-total {
  position: relative;
  padding: 20px;
  height: 40px;
  background: #fafafa;
  border-top: 1px solid #f0f0f0;
  border-radius: 0 0 8px 8px;
  -webkit-box-shadow: inset 0 -1px 0 rgba(255, 255, 255, .5), 0 -3px 8px rgba(0, 0, 0, .04);
          box-shadow: inset 0 -1px 0 rgba(255, 255, 255, .5), 0 -3px 8px rgba(0, 0, 0, .04);
  background: -webkit-gradient(linear, left top, left bottom, from(#fafafa), to(#f5f5f5));
  background: linear-gradient(#fafafa, #f5f5f5);
}

.nav-cart-list .nav-cart-total p {
  margin-bottom: 4px;
  line-height: 16px;
  font-size: 13px;
  color: #c1c1c1;
}

.nav-cart-list .nav-cart-total h5 {
  line-height: 20px;
  font-size: 14px;
  color: #6f6f6f;
}

.nav-cart-list .nav-cart-total h5 span {
  font-size: 18px;
  color: #de4037;
  display: inline-block;
  font-weight: 700;
}

.nav-cart-list .nav-cart-total h5 span:first-child {
  font-size: 12px;
  margin-right: 5px;
}

.nav-cart-list .nav-cart-total h5 span {
  font-size: 18px;
  color: #ff6700;
  display: inline-block;
  font-weight: 700;
}

.nav-cart-list .nav-cart-total h6 {
  position: absolute;
  right: 20px;
  top: 20px;
  width: 108px;
}

.nav-aside .nav-cart-btn {
  display: block;
  height: 40px;
  background: #ff6700;
  -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, .2), 0 1px 3px rgba(0, 0, 0, .2);
          box-shadow: inset 0 1px 0 rgba(255, 255, 255, .2), 0 1px 3px rgba(0, 0, 0, .2);
  line-height: 40px;
  text-align: center;
  color: #FFF;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, .15)
}

/*次导航*/

.nav-sub {
  position: relative;
  z-index: 20;
  height: 90px;
}

.nav-sub-wrapper {
  padding: 31px 0;
  height: 28px;
  position: relative;
}

.nav-sub .nav-list {
  float: left;
  height: 28px;
  line-height: 28px;
}

.nav-sub .nav-list li {
  position: relative;
  float: left;
  padding-left: 2px;
}

.nav-sub .nav-list li:first-child {
  padding-left: 0;
}

.nav-sub .nav-list li:before {
  content: ' ';
  position: absolute;
  left: 0;
  top: 13px;
  width: 2px;
  height: 2px;
  background: #bdbdbd;
}

.nav-sub .nav-list li:first-child:before {
  display: none;
}

.nav-sub .nav-list li a {
  display: block;
  padding: 0 20px;
  color: #666;
}

.nav-sub .nav-list li:first-child a {
  padding-left: 10px;
}

.nav-sub .nav-list li.active a {
  font-weight: 700;
}

.nav-sub .nav-list li a:hover {
  color: #6b95ea;
}@charset "utf-8";
