.sku-box{
    position: relative;
}
.sort-option{
    overflow: hidden;
    border-top: 1px solid #D8D8D8;
    color: #999;
}
.sort-option ul{
    height: 60px;
    line-height: 60px;
}
.sort-option ul.left{
    margin-top: 88px;
    float: left;
}
.sort-option ul.right{
    margin-top: 100px;
    float: right;
}
.sort-option li{
    position: relative;
    float: left;
    padding-left: 42px;
}
.sort-option li:first-child{
	padding-left: 9px;
}
.right.arrow.before{
    content: "";
    position: absolute;
    top: 16px;
    color: #343434;
    right: -17px;
    width: 0;
    height: 0;
    border: 5px solid transparent;
    border-bottom-color: inherit;
}
.right.arrow.after{
    content: "";
    position: absolute;
    bottom: 18px;
    right: -17px;
    color: #343434;
    width: 0;
    height: 0;
    border: 5px solid transparent;
    border-top-color: inherit;
}
.sort-option li:first-child:before{
	display: none;
}
.sort-option a.left{
    display: block;
    font-weight: normal;
    letter-spacing: 2px;
    font-size: 28px;
    color: #333;
}
.sort-option a.right{
    display: block;
    font-size: 18px;
    color: #343434;
}
.p_arrow-top{
    position: absolute;
    top: 33px;
    left: 89px;
    float: left;
    border-width: 12px;
    border-style: solid;
    border-color:#fff transparent transparent transparent;
}
.p_arrow-bottom{
    position: absolute;
    top: 3px;
    left:88px;
    float: left;
    border-width: 12px;
    border-style: solid;
    border-color:#fff transparent transparent transparent;
    transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
}
.sort-option a.active, .sort-option a:hover{
    color:#00c3f5;
    transition: .2s all;
    -webkit-transition: .2s all;
    -moz-transition: .2s all;
    -ms-transition: .2s all;
    -o-transition: .2s all;
}
